import React from "react";
import styled from "styled-components";
import arrow from "../assets/shared/desktop/arrow.svg";

const StyledArrowLink = styled.a`
    text-decoration: none;
    color: ${(props) => props.color};
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 0.75rem;
    line-height: 15.62px;
    display: flex;
    justify-content: ${(props) =>
        props.justify ? "space-between" : "flex-start"};
    align-items: center;
    transition: all 0.15s;
    position: relative;
    width: 200px;
    padding-block: 0.5rem;

    &::after {
        content: "";
        background: ${(props) =>
            props.color === "#FFF" ? "#FFFFFF85" : "#000"};
        height: 1px;
        position: absolute;
        bottom: 0;
        width: 0;
        transition: all 0.3s;
    }

    &:hover {
        color: ${(props) => props.color};
        cursor: pointer;

        &::after {
            width: 100%;
        }
    }

    img {
        filter: ${(props) =>
            props.color === "#FFF" ? "invert(1) brightness(100)" : "initial"};
        margin-left: 1.125rem;
    }
`;

export default function ArrowLink(props) {
    return (
        <StyledArrowLink color={props.color} justify={props.justify}>
            {props.children}
            <img src={arrow} />
        </StyledArrowLink>
    );
}
