import React from "react";
import FeatureList from "./FeatureList";
import Footer from "./Footer";
import Gallery from "./Gallery";
import MainContent from "./MainContent";
import {homeData, homeGallery, homeFeatures} from "../data.js"
import Fade from "react-reveal"

export default function Home(props) {
    console.log(homeData)
    return (
        <section id="home">
            {homeData.map((content, index) => {
                return (
                    <Fade left>
                    <MainContent
                        key={index}
                        src={content.src.slice(2)}
                        heading={content.heading}
                        description={content.description}
                        background={content.background}
                        link={content.link}
                        color={content.background === "#000" ? "#FFF" : "#000"}
                        margin={index !== 0 ? "0" : "72px"}
                        order={index % 2 === 0 ? 0 : 1}
                        gradient={content.gradient}
                        changeActivePage={props.changeActivePage}
                    />
                    </Fade>
                );
            })}
            <Gallery data={homeGallery} />
            <FeatureList features={homeFeatures}/>
            <Footer changeActivePage={props.changeActivePage}/>
        </section>
    );
}
