import React from "react";
import styled from "styled-components";
import ArrowLink from "../UI/ArrowLink";

const StyledWrapper = styled.div`
    width: 100vw;
    background-image: url(${require("../assets/shared/mobile/bg-beta.jpg").default});
    background-repeat: no-repeat;
    background-size: cover;
    padding: 4rem 2rem;
    position: relative;

    @media all and (min-width: 768px) {
        background-image: url(${require("../assets/shared/tablet/bg-beta.jpg").default});
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 4.25rem 5.5%;
    }

    @media all and (min-width: 1440px) {
        padding: 4.25rem 12%;
        background-image: url(${require("../assets/shared/desktop/bg-beta.jpg").default});
    }

    &::after {
        content: "";
        background: linear-gradient(
            167deg,
            rgba(255, 197, 147, 1) 0%,
            rgba(188, 113, 152, 1) 53%,
            rgba(90, 119, 255, 1) 100%
        );
        position: absolute;
        top: 0;
        left: 10%;
        height: 5px;
        width: 33%;

        @media all and (min-width: 768px) {
            left: 0;
            height: 100%;
            width: 5px;
        }
    }

    h1 {
        font-size: 2rem;
        font-weight: 700;
        line-height: 40px;
        letter-spacing: 3.3px;
        text-transform: uppercase;
        color: #fff;
        margin-bottom: 1rem;

        @media all and (min-width: 768px) {
            font-size: 2.5rem;
            line-height: 48px;
            letter-spacing: 0.2606rem;
            width: 50%;
        }
    }
`;

export default function Beta() {
    return (
        <StyledWrapper>
            <h1>We're in beta. Get your invite today!</h1>
            <ArrowLink color="#FFF">get an invite</ArrowLink>
        </StyledWrapper>
    );
}
