import "bootstrap/dist/css/bootstrap.min.css";
import GlobalStyles from "./Styles/GlobalStyles";
import Navbar from "./UI/Navbar";
import Home from "./components/Home";
import Stories from "./components/Stories";
import FeaturesPage from "./components/FeaturesPage";
import { useState } from "react";
import PricingPage from "./components/PricingPage";

function App() {
  const [activePage, setActivePage] = useState("home")

  const changeActivePage = (page) => {
    if (page === activePage) {
      return activePage
    }
    setActivePage(page)
    return false
  }

    return (
        <>
            <GlobalStyles />
            <div className="App">
                <Navbar changeActivePage={changeActivePage}/>
                {activePage === "home" && <Home changeActivePage={changeActivePage}/>}
                {activePage === "stories" && <Stories changeActivePage={changeActivePage} activePage={activePage}/>}
                {activePage === "features" && <FeaturesPage changeActivePage={changeActivePage} />}
                {activePage === "pricing" && <PricingPage changeActivePage={changeActivePage} />}
            </div>
        </>
    );
}

export default App;
