import React from "react";
import styled from "styled-components";

const SwtichContainer = styled.div`
    position: relative;
    width: 64px;
    height: 32px;

    input[type="checkbox"] {
        width: 32px;
        height: 32px;
        opacity: 0;

        &:checked + label::before {
            left: 36px;
            background: #fff;
        }

        &:checked + label {
            background: #000;
        }
    }

    label {
        background: #dfdfdf;
        border-radius: 15px;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transition: all 0.2s;

        &::before {
            position: absolute;
            content: "";
            width: 24px;
            height: 24px;
            background: #000;
            border-radius: 50%;
            left: 4px;
            top: 50%;
            transform: translateY(-50%);
            transition: all 0.3s;
        }
    }
`;

export default function Switch(props) {
    return (
        <SwtichContainer>
            <input onChange={props.onChange} type="checkbox" id="switch" name="switch" />
            <label htmlFor="switch"></label>
        </SwtichContainer>
    );
}
