import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import Story from "./Story";
import Slide from "react-reveal";

export default function Gallery(props) {
    // const desktopImg = props.src.replace("mobile", "desktop").slice(3);

    return (
        <Container fluid className="m-0">
            <Slide right>
                <Row className="p-0">
                    {props.data.map((photo, index) => {
                        return (
                            <Col
                                sm={12}
                                md={6}
                                xl={3}
                                className="p-0"
                                key={index}
                            >
                                <Story
                                    key={index}
                                    src={photo.src}
                                    title={photo.title}
                                    author={photo.author}
                                    date={photo.date}
                                />
                            </Col>
                        );
                    })}
                </Row>
            </Slide>
        </Container>
    );
}
