import React from "react";
import styled from "styled-components";
import ArrowLink from "../UI/ArrowLink";
import Zoom from "react-reveal";

const Container = styled.div`
    position: relative;
    transition: all 0.3s ease-in-out;
    overflow: hidden;

    &::after {
        content: "";
        position: absolute;
        inset: 0;
        background: linear-gradient(
            175deg,
            rgba(255, 255, 255, 1) 0%,
            rgba(0, 0, 0, 0.7) 100%
        );
        mix-blend-mode: multiply;
    }

    @media all and (min-width: 1440px) {
        &:hover {
            transform: translateY(-2.5%);
            cursor: pointer;

            & > img {
                transform: scale(1.12);
            }
        }
    }
`;

const Img = styled.img`
    transition: all 0.3s ease-in-out;
    width: 100%;

    &.mobile {
        @media all and (min-width: 720px) {
            display: none;
        }
    }

    &.desktop {
        display: none;

        @media all and (min-width: 720px) {
            display: initial;
        }
    }
`;

const Content = styled.div`
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: 2;
    bottom: 0;
    left: 0;
    padding: 2.5rem 2rem;
    width: 100%;
`;

const Title = styled.h3`
    color: #fff;
    font-size: 1.125rem;
    font-weight: 700;
    line-height: 1.25rem;
`;

const Author = styled.p`
    color: #fff;
    font-size: 0.8125rem;
    line-height: 1.0625rem;
    padding-bottom: 1rem;
    display: block;
    border-bottom: 1px solid #ffffff50;
`;

const Date = styled.p`
    color: #fff;
    font-size: 0.8125rem;
    line-height: 1.0625rem;
    padding-bottom: 0.25rem;
    margin: 0;
`;

export default function Story(props) {
    const mobileImg = props.src.slice(2);
    const desktopImg = props.src.slice(2).replace("mobile", "desktop");

    return (
        <Zoom>
            <Container>
                <Img
                    className="mobile"
                    src={require(`../${mobileImg}`).default}
                    alt={`photo by ${props.author}`}
                />
                <Img
                    className="desktop"
                    src={require(`../${desktopImg}`).default}
                    alt={`photo by ${props.author}`}
                />
                <Content>
                    <Date>{props.date}</Date>
                    <Title>{props.title}</Title>
                    <Author>by {props.author}</Author>
                    <ArrowLink color="#FFF" justify="space-between">
                        Read story
                    </ArrowLink>
                </Content>
            </Container>
        </Zoom>
    );
}
