import React from "react";
import styled from "styled-components";
import { planFeatures } from "../data";
import check from "../assets/pricing/desktop/check.svg";

const Container = styled.div`
    width: 80%;
    margin: 4rem auto;

    @media (min-width: 768px) {
        display: none;
    }
`

const Heading = styled.h3`
    font-size: 0.75rem;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding-bottom: 1.4375rem;
    border-bottom: 1px solid #000;
    font-weight: 700;
`;

const FeatureContainer = styled.div`
    margin-block: 1.5rem;
    padding-bottom: 1.5rem;
    border-bottom: 1px solid #00000050;
`;

const Feature = styled.h4`
    font-size: 0.75rem;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 1rem;
`;

const Plans = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 4.375rem;

    .plan {
        grid-row: 1 / 2;
        color: #00000080;
        text-transform: uppercase;
        font-size: 0.625rem;
        letter-spacing: 2px;
        margin-bottom: 0.5rem;
        font-weight: 700;
    }

    .check {
        grid-row: 2 / 3;

        &.basic {
            grid-column: 1 / 2;
        }

        &.pro {
            grid-column: 2 / 3;
        }

        &.business {
            grid-column: 3 / 4;
        }
    }
`;

export default function MobilePlanTable() {
    return (
        <Container>
            <Heading>the features</Heading>
            {planFeatures.map((feature, index) => {
                return (
                    <FeatureContainer>
                        <Feature>{feature.feature}</Feature>
                        <Plans>
                            <span className="plan">basic</span>
                            {feature.basic && (
                                <img src={check} className="check basic" />
                            )}
                            <span className="plan">pro</span>
                            {feature.pro && (
                                <img src={check} className="check pro" />
                            )}
                            <span className="plan">business</span>
                            {feature.business && (
                                <img src={check} className="check business" />
                            )}
                        </Plans>
                    </FeatureContainer>
                );
            })}
        </Container>
    );
}
