import React from "react";
import styled from "styled-components";
import ArrowLink from "../UI/ArrowLink";
import Logo from "../UI/Logo";
import FooterLinks from "./FooterLinks";
import SocialMediaIcons from "./SocialMediaIcons";

const StyledFooter = styled.footer`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #000;
    padding: 3.5rem;
    color: #fff;

    @media all and (min-width: 768px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        row-gap: 2rem;

        .logo,
        .links,
        .icons {
            justify-self: start;
            grid-column: 1 / 2;
        }

        .icons {
            grid-row: 3 / 4;
        }

        .arrow {
            grid-column: 2 / 3;
            grid-row: 1 / 2;
            justify-self: end;
        }
    }

    @media all and (min-width: 1200px) {
        padding-inline: 12%;
        grid-template-columns: 0.3fr 1fr 0.3fr;
        column-gap: 2rem;

        .links {
            grid-column: 2 / 3;
            grid-row: 1 / 4;
            height: 100%;
        }

        .arrow {
            grid-column: 3 / 4;
        }
    }
`;

const Copyright = styled.span`
    color: #ffffff50;
    font-size: 0.9375rem;
    margin-top: 2.125rem;
    grid-column: 2 / 3;
    grid-row: 3 / 4;

    @media all and (min-width: 768px) {
        justify-self: end;
        margin: 0;
    }

    @media all and (min-width: 1200px) {
        grid-column: 3 / 4;
    }
`;

export default function Footer(props) {
    return (
        <StyledFooter>
            <div className="logo">
                <Logo color="white" />
            </div>
            <div className="icons">
                <SocialMediaIcons />
            </div>
            <div className="links">
                <FooterLinks changeActivePage={props.changeActivePage}/>
            </div>
            <div className="arrow">
                <ArrowLink color="#FFF">get an invite</ArrowLink>
            </div>
            <Copyright>Copyright 2019. All Rights Reserved</Copyright>
        </StyledFooter>
    );
}
