import React from "react";
import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-family: 'DM Sans', sans-serif;
    }

    a {
        color: #FFFFFF30
    }

    body, html {
        overflow-x: hidden;
    }
`;

export default function GlobalStyles() {
    return <GlobalStyle />;
}
