import React from "react";
import styled from "styled-components";
import { Container, Row, Col } from "react-bootstrap";
import ArrowLink from "../UI/ArrowLink";
import bg from "../assets/stories/tablet/moon-of-appalacia.jpg";
import bgDesktop from "../assets/stories/desktop/moon-of-appalacia.jpg";

const StyledContent = styled.div`
    background-color: ${(props) => props.background};
    color: ${(props) => (props.background ? "#FFF" : "#000")};
    padding: 4.5rem 8.8%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;

    @media all and (min-width: 768px) {
        padding: 7.625rem 10%;
        background-color: ${(props) =>
            props.activePage === "stories" ? "transparent" : props.background};
    }

    @media all and (min-width: 1200px) {
        padding-inline: 7rem 7.7%;
    }

    p {
        color: ${(props) => (props.background ? "#FFFFFF60" : "#000")};
    }
`;

const ImgContainer = styled.div`
    .mobile {
        @media all and (min-width: 768px) {
            display: none;
        }
    }

    .tablet {
        display: none;

        @media all and (min-width: 768px) {
            display: initial;
        }

        @media all and (min-width: 1200px) {
            display: none;
        }
    }

    .desktop {
        display: none;

        @media all and (min-width: 1200px) {
            display: initial;
        }
    }
`;

const StyledHeading = styled.h2`
    color: ${(props) => props.color};
    font-size: 2rem;
    font-weight: 700;
    line-height: 2.5rem;
    letter-spacing: 3.33px;
    margin-bottom: 1rem;
    text-transform: uppercase;

    @media all and (min-width: 768px) {
        font-size: 2.5rem;
        line-height: 3rem;
        letter-spacing: 4.17px;
        width: 90%;

        &::before,
        &::after {
            font-size: 0.75rem;
            display: block;
        }

        &::before {
            content: "${(props) => props.before}";
        }

        &::after {
            content: "${(props) => props.after}";
            font-weight: 400;
            text-transform: capitalize;
            letter-spacing: 3px;
            opacity: 0.8;
        }
    }

    @media all and (min-width: 1200px) {
        width: 100%;
    }
`;

const StyledDescription = styled.p`
    font-size: 0.9375rem;
    line-height: 25px;
    margin-bottom: 1.4375rem;

    @media all and (min-width: 768px) {
        width: 80%;
    }
`;

const Test = styled.div`
    position: absolute;
    inset: 0;
    background-image: url(${bg});
    background-repeat: no-repeat;
    background-size: cover;

    @media all and (min-width: 1200px) {
        background-image: url(${bgDesktop});
    }
`;

export default function MainContent(props) {
    const tabletImg = props.src.replace("mobile", "tablet");
    const desktopImg = props.src.replace("mobile", "desktop");

    const handleClick = () => {
        if (props.link.includes("stories")) {
            props.changeActivePage("stories");
        } else {
            return null;
        }
    };

    return (
        <Container fluid className="p-0" style={{ marginTop: props.margin }}>
            <Row className="mt-0" style={{ position: "relative" }}>
                {props.activePage === "stories" && <Test />}
                <Col
                    sm={12}
                    md={{ span: 4, order: props.order === 1 ? 0 : 1 }}
                    xl={7}
                    className="p-0"
                >
                    <ImgContainer>
                        <img
                            className="mobile"
                            width="100%"
                            src={require(`../${props.src}`).default}
                            alt="img"
                        />
                        <img
                            className="tablet"
                            width="100%"
                            src={require(`../${tabletImg}`).default}
                            alt="img"
                        />
                        <img
                            className="desktop"
                            width="100%"
                            src={require(`../${desktopImg}`).default}
                            alt="img"
                        />
                    </ImgContainer>
                </Col>
                <Col
                    className="p-0"
                    sm={12}
                    md={{
                        span: props.activePage === "stories" ? 7 : 8,
                        order: props.order,
                    }}
                    xl={5}
                >
                    <StyledContent
                        background={props.background}
                        gradient={props.gradient}
                        fullscreen={props.fullscreen}
                        activePage={props.activePage}
                    >
                        <StyledHeading
                            before={props.before}
                            after={props.after}
                        >
                            {props.heading}
                        </StyledHeading>
                        <StyledDescription>
                            {props.description}
                        </StyledDescription>
                        {props.link && (
                            <div onClick={handleClick}>
                                <ArrowLink color={props.color}>
                                    {props.link}
                                </ArrowLink>
                            </div>
                        )}
                    </StyledContent>
                </Col>
            </Row>
        </Container>
    );
}
