import React from "react";
import styled from "styled-components";
import check from "../assets/pricing/desktop/check.svg";
import { planFeatures } from "../data";

const Container = styled.div`
    display: none;

    @media all and (min-width: 768px) {
        display: initial;
    }
`

const Table = styled.table`
    width: 85%;
    margin: 0 auto 4rem;

    @media all and (min-width: 768px) {
        margin-bottom: 7rem;
    }

    @media all and (min-width: 1200px) {
        width: 50%;
        margin-bottom: 10rem;
    }

    th {
        padding-inline: 1.5rem;
    }

    tr {
        border-bottom: 1px solid #00000020;
    }

    thead {
        border-bottom: 1px solid #000;

        th {
            padding-bottom: 1.5rem;
        }

        & > tr {
            border: none;
        }
    }

    td {
        padding: 1.5rem;

        &.check {
            text-align: center;
        }
    }

    &.mobile {
        @media all and (min-width: 768px) {
            display: none;
        }
    }
`;

const Th = styled.th`
    text-transform: uppercase;
    font-weight: 700;
    font-size: 0.75rem;
    letter-spacing: 2px;

    &.plan {
        visibility: hidden;

        @media all and (min-width:768px) {
            visibility: initial;
            text-align: center;
        }
    }
`;

const StyledSpan = styled.span`
    display: none;

    @media all and (min-width: 768px) {
        display: block;
        font-size: 2.5rem;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 4.2px;
        margin-block: 7rem 4rem; 
        text-align: center;
    }

    @media all and (min-width: 1200px) {
        margin-block: 10rem 3.5rem;
    }
`;

export default function PlanTable(props) {
    return (
        <Container>
            <StyledSpan>Compare</StyledSpan>
            <Table className="tablet">
                <thead>
                    <tr>
                        <Th>The features</Th>
                        <Th className="plan">basic</Th>
                        <Th className="plan">pro</Th>
                        <Th className="plan">business</Th>
                    </tr>
                </thead>
                <tbody>
                    {planFeatures.map((feature, index) => {
                        return (
                            <tr key={index}>
                                <Th className="feature">{feature.feature}</Th>
                                <td className="check">
                                    {feature.basic && (
                                        <img src={check} alt="check icon" />
                                    )}
                                </td>
                                <td className="check">
                                    {feature.pro && (
                                        <img src={check} alt="check icon" />
                                    )}
                                </td>
                                <td className="check">
                                    {feature.business && (
                                        <img src={check} alt="check icon" />
                                    )}
                                </td>
                            </tr>
                        );
                    })}
                </tbody>
            </Table>
        </Container>
    );
}
