import React from "react";
import { features } from "../data";
import Beta from "./Beta";
import FeatureList from "./FeatureList";
import MainContent from "./MainContent";
import Footer from "./Footer";
import Fade from "react-reveal";

export default function FeaturesPage(props) {
    return (
        <section id="features" style={{ marginTop: "90px" }}>
            <Fade left>
                <MainContent
                    src="assets/features/mobile/hero.jpg"
                    background="#000"
                    heading="Features"
                    description="We make sure all of our features are designed to be loved by every aspiring and even professional photograpers who wanted to share their stories."
                />
            </Fade>
            <FeatureList features={features} />
            <Beta />
            <Footer changeActivePage={props.changeActivePage}/>
        </section>
    );
}
