import React from "react";
import Fade from "react-reveal";
import Gallery from "./Gallery";
import MainContent from "./MainContent";
import { storiesGallery } from "../data";
import Footer from "./Footer";

export default function Stories(props) {
    return (
        <section id="stories" style={{marginTop: "97px"}}>
            <Fade left>
                <MainContent
                    background="#000"
                    before="LAST MONTH’S FEATURED STORY"
                    after="March 2nd 2020"
                    heading="hazy full moon of appalachia"
                    src="assets/stories/mobile/moon-of-appalacia.jpg"
                    description='The dissected plateau area, while not actually made up of geological mountains, is popularly called "mountains," especially in eastern Kentucky and West Virginia, and while the ridges are not high, the terrain is extremely rugged.'
                    link="read the story"
                    color= "#FFF"
                    activePage={props.activePage}
                />
            </Fade>
            <Gallery data={storiesGallery}/>
            <Footer changeActivePage={props.changeActivePage}/>
        </section>
    );
}
