import React from "react";
import styled from "styled-components";
import Button from "../UI/Button";

const Card = styled.div`
    background-color: ${(props) => (props.pro ? "#000" : "#F5F5F5")};
    padding: 3.5rem 7% 2.5rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: ${(props) => (props.pro ? "#FFF" : "#000")};
    text-align: center;
    width: 85%;
    margin-bottom: 1.5rem;
    transition: all 0.25s;

    &:hover {
        transform: scale(${props => props.pro ? "1.20" : "1.05"});
    }

    @media all and (min-width: 768px) {
        text-align: left;
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    @media all and (min-width: 1200px) {
        display: flex;
        flex-direction: column;
        text-align: center;
        transform: scale(${props => props.pro && "1.15"});
    }
`;

const Plan = styled.h2`
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1.125rem;

    @media all and (min-width: 768px) {
        grid-column: 1 / 2;
    }
`;

const Description = styled.p`
    font-size: 0.9375rem;
    line-height: 25px;
    margin-bottom: 2.5rem;

    @media all and (min-width: 768px) {
        grid-column: 1 / 2;
    }
`;

const Cost = styled.span`
    display: block;
    font-size: 2.5rem;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 4.2px;
    margin-bottom: 4.0625rem;

    @media all and (min-width: 768px) {
        grid-column: 2 / 3;
        grid-row: 1 / 3;
        justify-self: end;
    }

    &::after {
        content: "per month";
        display: block;
        font-weight: 400;
        font-size: 0.9375rem;
        line-height: 25px;
        opacity: 0.7;
    }
`;

export default function PricingCard(props) {
    return (
        <Card pro={props.pro}>
            <Plan>{props.plan}</Plan>
            <Description>{props.description}</Description>
            <Cost>{`$${props.cost}`}</Cost>
            <Button background={props.background}>pick plan</Button>
        </Card>
    );
}
