import React, { useState } from "react";
import styled from "styled-components";

import open from "../assets/shared/mobile/menu.svg";
import close from "../assets/shared/mobile/close.svg";
import Button from "./Button";

import Logo from "./Logo";

const StyledNavbar = styled.nav`
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;
    align-items: center;
    padding: 1.75rem 1.5rem 1.3rem;
    position: fixed;
    top: 0;
    background-color: #fff;
    z-index: 9;

    @media all and (min-width: 768px) {
        padding: 1rem 2.5rem;
        display: flex;
        justify-content: space-between;
    }

    @media all and (min-width: 1200px) {
        justify-content: space-around;
    }

    .Logo,
    .navbar-btn {
        z-index: 3;
        position: relative;
    }

    .navbar-btn {
        justify-self: end;

        @media all and (min-width: 768px) {
            display: none;
        }
    }

    button {
        grid-column: 1 / 3;
    }
`;

const StyledNavItems = styled.div`
    grid-column: 1 / 3;
    text-align: center;
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    transform: translateY(-180%);
    transition: all 0.5s ease-in-out;
    position: relative;
    max-height: 0;
    overflow: hidden;

    @media all and (min-width: 768px) {
        grid-column: 2 / 3;
        transform: translateY(0);
        max-height: 500px;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }

    @media all and (min-width: 1200px) {
        width: 50%;
    }

    &.open {
        transform: translateY(0);
        max-height: 500px;
        border-bottom: 1px solid #00000025;
        margin-bottom: 2rem;

        @media all and (min-width: 768px) {
            border: none;
            margin: 0;
        }
    }

    .nav-link {
        text-decoration: none;
        letter-spacing: 2.5px;
        font-weight: 700;
        color: #000;
        margin-bottom: 1.25rem;
        position: relative;
        padding: 0;

        &:hover {
            color: #00000030;
        }

        @media all and (min-width: 768px) {
            margin: 0;
            margin-right: 2.3125rem;
            font-size: 0.75rem;
        }

        &:first-of-type {
            margin-top: 2rem;
            @media all and (min-width: 768px) {
                margin-top: 0;
            }
        }

        &:last-of-type {
            margin-bottom: 2.5rem;
            @media all and (min-width: 768px) {
                margin: 0;
            }

            &::after {
                content: "";
                background-color: #00000025;
                height: 1px;
                width: 100%;
                position: absolute;
                bottom: -15px;
                left: 0;

                @media all and (min-width: 768px) {
                    content: none;
                }
            }
        }
    }

    .tablet-nav-links {
        @media all and (min-width: 768px) {
            display: flex;
            margin-right: 10%;
        }
    }
`;

export default function Navbar(props) {
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    const handleMenu = () => {
        setIsMenuOpen((prev) => !prev);
    };

    const handleLink = (page) => {
        setIsMenuOpen(false);
        props.changeActivePage(page);
        return false;
    };

    return (
        <StyledNavbar>
            <div onClick={() => props.changeActivePage("home")}>
                <Logo
                    className="Logo"
                />
            </div>
            <div className="navbar-btn" onClick={handleMenu}>
                <img src={isMenuOpen ? close : open} alt="navbar button" />
            </div>
            <StyledNavItems className={isMenuOpen && "open"}>
                <div className="tablet-nav-links">
                    <a
                        className="nav-link"
                        href="#"
                        onClick={() => handleLink("stories")}
                    >
                        stories
                    </a>
                    <a
                        className="nav-link"
                        href="#"
                        onClick={() => handleLink("features")}
                    >
                        features
                    </a>
                    <a
                        className="nav-link"
                        href="#"
                        onClick={() => handleLink("pricing")}
                    >
                        pricing
                    </a>
                </div>
                <Button>get an invite</Button>
            </StyledNavItems>
        </StyledNavbar>
    );
}
