import React from 'react'
import styled from 'styled-components'

const Container = styled.div`
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 7.4375rem;

    @media all and (min-width: 768px) {
        flex-direction: row;
        justify-content: space-between;
        width: 19.6875rem;
        margin-bottom: 0;
    }

    @media all and (min-width: 1200px) {
        flex-direction: column;
        align-items: flex-start;
    }
`

const Link = styled.a`
    color: #FFF;
    margin-bottom: 1.1875rem;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 0.75rem;
    letter-spacing: 2px;
    font-weight: 700;
    transition: all 0.25s;

    &:hover {
        color: #FFFFFF30;
        cursor: pointer;
    }

    @media all and (min-width: 1200px) {
        &:last-child {
            margin: 0;
        }
    }
`

export default function FooterLinks(props) {
    const handleClick = (page) => {
        props.changeActivePage(page)
        return false;
    }

    return (
        <Container>
            <Link href="#" onClick={() => handleClick("home")}>home</Link>
            <Link href="#" onClick={() => handleClick("stories")}>stories</Link>
            <Link href="#" onClick={() => handleClick("features")}>features</Link>
            <Link href="#" onClick={() => handleClick("pricing")}>pricing</Link>
        </Container>
    )
}
