import React from "react";
import styled from "styled-components";
import facebook from "../assets/shared/desktop/facebook.svg";
import instagram from "../assets/shared/desktop/instagram.svg";
import pinterest from "../assets/shared/desktop/pinterest.svg";
import twitter from "../assets/shared/desktop/twitter.svg";
import youtube from "../assets/shared/desktop/youtube.svg";

const icons = [
    { icon: facebook, href: "https://www.facebook.com/" },
    {icon: instagram, href: "https://www.instagram.com/"},
    {icon: pinterest, href: "https://www.pinterest.com/"},
    {icon: twitter, href: "https://www.twitter.com/"},
    {icon: youtube, href: "https://www.youtube.com/"}
];

const SocialMediaContainer = styled.div`
    display: flex;
    justify-content: space-between;
    margin-block: 2rem 3.0625rem;

    @media all and (min-width: 768px) {
        margin-bottom: 0;
    }

    @media all and (min-width: 1200px) {
        margin-top: 0;
    }
`;
const Link = styled.a`
    margin-right: 0.8125rem;

    &:last-of-type {
        margin: 0;
    }

    img {
        filter: brightness(0) invert(1);
        transition: all 0.25s;

        &:hover {
            filter: none;
        }
    }
`;

export default function SocialMediaIcons() {
    return (
        <SocialMediaContainer>
            {icons.map((icon, index) => {
                return (
                    <Link key={index} href={icon.href}>
                        <img src={icon.icon} alt="social media icon" />
                    </Link>
                );
            })}
        </SocialMediaContainer>
    );
}
