import React, { useState } from "react";
import Switch from "../UI/Switch";
import styled from "styled-components";
import PricingPlans from "./PricingPlans";

const SwtichContainer = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4rem auto 2.5rem;
    width: 15.9375rem;

    @media all and (min-width: 1200px) {
        margin-top: 7rem;
        margin-bottom: 5rem;
    }

    span {
        font-size: 1.125rem;
        line-height: 1.5625rem;
        transition: all 0.25s;

        &.selected {
        }
    }

    .selected {
        font-weight: 700;
    }
`;

export default function PricingSection() {
    const [pay, setPay] = useState("monthly");
    const handleChange = () => {
        setPay(pay === "monthly" ? "yearly" : "monthly");
    };

    return (
        <div>
            <SwtichContainer>
                <span className={pay === "monthly" && "selected"}>Monthly</span>
                <Switch onChange={handleChange} />
                <span className={pay === "yearly" && "selected"}>Yearly</span>
            </SwtichContainer>
            <PricingPlans pay={pay}/>
        </div>
    );
}
