import React from "react";
import MainContent from "./MainContent";
import PricingSection from "./PricingSection";
import Fade from "react-reveal";
import PlanTable from "./PlanTable";
import Beta from "./Beta";
import Footer from "./Footer";
import MobilePlanTable from "./MobilePlanTable";

export default function PricingPage(props) {
    return (
        <section id="pricing" style={{ marginTop: "90px" }}>
            <Fade right>
                <MainContent
                    src="assets/pricing/mobile/hero.jpg"
                    heading="pricing"
                    description="Create a your stories, Photosnap is a platform for photographers and visual storytellers. It’s the simple way to create and share your photos."
                    background="#000"
                />
            </Fade>
            <Fade left>
                <PricingSection />
            </Fade>
            <MobilePlanTable />
            <PlanTable />
            <Beta />
            <Footer changeActivePage={props.changeActivePage}/>
        </section>
    );
}
